.salesT-con {
  .salesT-view {
    .salesT-header {
      margin-bottom: 19px;
      .salesT-line {
        display: inline-block;
        width: 4px;
        height: 20px;
        margin-right: 8px;
        vertical-align: middle;
        background-color: #217ba0;
        border-radius: 4px;
      }
      .salesT-text {
        color: #000;
        font-size: 14px;
        vertical-align: middle;
      }
    }

    .salesT-pages {
      margin-top: 10px;
      text-align: right;
    }
  }
  .orgEdit {
    color: #217ba0;
  }
  .salesT-Mr {
    margin-right: 17px;
  }

  .orgDelete {
    color: #f53f3f;
  }
  .orgEnable {
    background: #7ed321;
  }
  .orgDisable {
    background: #f53f3f;
  }
  .orgLabel {
    display: inline-block;
    width: 6px;
    height: 6px;
    margin-right: 10px;
    vertical-align: middle;
    border-radius: 6px;
  }
  .description{
    width: 100%;
    overflow: hidden;
    white-space: nowrap;
    text-overflow: ellipsis;
  }
}
